.icon_wrap {
  border-radius: 15px;
}
.icon_wrap:hover,
.icon_wrap.active {
  box-shadow: 0px 10px 40px -16px #9bafeb;
  cursor: pointer;
}
.home_industry_content.active {
  display: block;
}
.service_steps_wrap::after {
  content: '';
  background-repeat: no-repeat;
  background-size: 25%;
  background-position: 0 center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.6;
}

.service_steps_wrap.page-seo::after {
  background-image: url('https://uploads-ssl.webflow.com/64a5cd902b5de3eaae8c3b76/64c28d1557bc6c407fb4a333_seo-blue-banner.svg');
}
.service_steps_wrap.page-ga::after {
  background-image: url('https://uploads-ssl.webflow.com/64a5cd902b5de3eaae8c3b76/64c2b95b0e8cc9ce53630fe6_ga-blue-banner.svg');
}
.service_steps_wrap.page-social-media::after {
  background-image: url('https://uploads-ssl.webflow.com/64a5cd902b5de3eaae8c3b76/64c2c244e961ce720a591c9f_social-blue-banner.svg');
}
.service_steps_wrap.page-ecommerce::after {
  background-image: url('https://uploads-ssl.webflow.com/64a5cd902b5de3eaae8c3b76/64c2c68fcbb995f9e7fec760_ecom-blue-banner.svg');
}
.service_steps_wrap.page-web::after {
  background-image: url('https://uploads-ssl.webflow.com/64a5cd902b5de3eaae8c3b76/64c2cc11987eb95da0bf5cd9_development-blue-banner.svg');
}
.recent_blog_title {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}
.recent_blog_title:hover {
  -webkit-line-clamp: 3;
}
